* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  width: 100%;
  height: 100vh;
  background: linear-gradient(0deg, #e5ed59 20%, #0091c3 85%) center / cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  color: #283e39;
  background: radial-gradient(circle, #edeeae 0%, #94bbe9 100%);
  border: 2px solid rgba(154, 167, 237, .4);
  border-radius: 8px;
  outline: none;
  margin: 30px 0 16px 50px;
  padding: 8px;
  font-weight: bold;
}

.country-list {
  padding-left: 55px;
  list-style: none;
}

.country-item {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.country-img {
  margin-right: 10px;
  display: block;
}

.country-title {
  margin: 0;
  font-size: 20px;
}

.country-card {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 400px;
  background-image: radial-gradient(circle, #090979 13%, #00d4ff 71%, #020024 100%);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 10px;
  flex-direction: column;
  padding: 30px;
  display: flex;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

.country-card-box {
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.country-card-svg {
  margin-right: 20px;
  display: block;
}

.country-card-title {
  color: #a0b0eb;
  margin: 0;
  font-size: 30px;
  font-style: italic;
  font-weight: bolder;
}

.country-card-text {
  margin-top: 0;
  padding: 0;
}

.country-card-text:not(:last-of-type) {
  margin-bottom: 15px;
}

.country-card-topic {
  color: #8f9393;
  margin-left: 15px;
  font-size: 20px;
  font-style: italic;
  font-weight: bold;
}

